.cost-date {
	display: flex;
	flex-direction: column;
	width: 5.5rem;
	height: 5.5rem;
	border: 1px solid #ececec;
	background-color: #2a5b84;
	color: #fefae1;
	border-radius: 12px;
	align-items: center;
	justify-content: center;
}

.cost-date__day {
	font-size: 1.3em;
	font-weight: bold;
}

.cost-date__month {
	font-size: 0.85em;
	font-weight: bold;
}

.cost-date__year {
	font-size: 0.85em;
}
